<template>
    <list-view src="/lambda/puzzle/schema/datasource" :title="lang.database + ' ' + lang.view" type="datasource" :data="listData" preview="hidden"></list-view>
</template>

<script>
    import listView from "../../../components/listview.vue";
    import {mapGetters} from "vuex";

    export default {
        components: {
            "list-view": listView
        },
        computed: {
            ...mapGetters({
                user: "user"
            }),
            lang() {
                const labels = ['database', 'view'];
                return labels.reduce((obj, key, i) => {
                    obj[key] = this.$t('dataSource.' + labels[i]);
                    return obj;
                }, {});
            },
        },
        methods:{

        }
    };
</script>
