<template>
    <section class="page page-list">
        <div class="page-list-header">
            <h3>{{ title }} ({{ listData.length }})</h3>
            <div class="page-list-header-search">
                <Input @on-keyup="filterList" icon="ios-search" :placeholder="lang.pleaseEnterSearchValue"
                       style="width: 200px"/>
            </div>
            <div class="page-list-header-control">
                <router-link :to="`${prefix ? prefix : ''}/${type}/builder`" class="btn-del">
                    <Button type="success">
                        <Icon type="md-add"></Icon>
                        {{lang.add}}
                    </Button>
                </router-link>
            </div>
        </div>

        <div v-if="loading" class="loader">
            <div class="loader-wrapper">
                <div class="lds-roller">
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
                <h3>{{lang.pleaseWait}}</h3>
            </div>
        </div>

        <Row v-else :gutter="16" class="pz-list"
             v-slimscroll="{height:'100%-30',size:7,alwaysVisible: true,wheelStep:7,color:'#2C3A47'}">
            <Col :xs="24" :sm="12" :md="8" :lg="6" v-for="item in filteredList" :key="item.index">
                <div class="pz-list-item">
                    <div class="pz-list-item-body">
                        <h3>{{ item.name }}</h3>
                        <small>{{ item.created_at }}</small>
                    </div>
                    <div class="pz-list-item-control">
                        <router-link :to="`${prefix ? prefix : ''}/${type}/preview/${item.id}`" class="btn-preview"
                                     v-if="preview != 'hidden'">
                            <Icon type="ios-eye"></Icon>
                        </router-link>&nbsp;
                        <a href="javascript:void(0);" class="btn-preview" v-if="type == 'form' || type == 'grid'"
                           @click="duplicate(item.id, type)">
                            <Icon type="md-copy"></Icon>
                        </a>
                        <span>
                        <router-link :to="`${prefix ? prefix : ''}/${type}/builder/${item.id}`" class="btn-edit">
                            <Icon type="md-create"></Icon>
                        </router-link>
                        <Poptip confirm :title="lang.deleteData" confirm width="180" :ok-text="lang.yes"
                                :cancel-text="lang.no" @on-ok="deleteListItem(item.id)" @on-cancel="cancel">
                            <a href="javascript:void(0)" class="btn-del">
                                <Icon type="ios-trash"></Icon>
                            </a>
                        </Poptip>
                    </span>
                    </div>
                </div>
            </Col>
        </Row>
        <Modal
            v-model="duplicateModal"
            :title="lang.copy"
            :ok-text="lang.copy"
            @on-ok="doDuplicate"
        >
            <label>{{lang.name}}</label>
            <Input v-model="duplicateData.name" :placeholder="lang.name" style="width: 100%"/>
            <label>{{ lang.table }} (DB table)</label>
            <Select v-if="type== 'form'" v-model="duplicateData.schema.model" :placeholder="lang.selectTable " clearable
                    style="width: 100%"
                    filterable>
                <Option v-for="item in tableList" :value="item" :key="item.index">{{ item }}</Option>
            </Select>

            <Select v-if="type== 'grid'" v-model="duplicateData.schema.model" :placeholder="lang.selectTable " clearable
                    filterable
                    style="width: 100%">
                <OptionGroup label="Table list">
                    <Option v-for="item in tableList" :value="item" :key="item.index">{{ item }}</Option>
                </OptionGroup>
                <OptionGroup label="View list">
                    <Option v-for="item in viewList" :value="item" :key="item.index">{{ item }}</Option>
                </OptionGroup>
            </Select>
            <label v-if="type== 'grid'">{{ lang.basicTable }} (DB table)</label>
            <Select v-if="type== 'grid'" v-model="duplicateData.schema.mainTable" :placeholder="lang.selectTable " clearable
                    style="width: 100%"
                    filterable>
                <Option v-for="item in tableList" :value="item" :key="item.index">{{ item }}</Option>
            </Select>
        </Modal>
    </section>
</template>

<script>

import {mapGetters} from "vuex";

export default {
    props: ["title", "type", "src", "preview", "prefix"],
    computed: {
        ...mapGetters({
            user: "user"
        }),
        lang() {
            const labels = [ 'add', 'pleaseEnterSearchValue', 'pleaseWait', 'deleteData', 'yes', 'no',
            'name', 'copy', 'table','basicTable', 'selectTable', ];
            return labels.reduce((obj, key, i) => {
                obj[key] = this.$t('components.' + labels[i]);
                return obj;
            }, {});
        },
        _form() {
            const labels = ['savedSuccessfull','formIformationSavedSuccessfull', 'successDeleted'];
            return labels.reduce((obj, key, i) => {
                obj[key] = this.$t('alertMessage.' + labels[i]);
                return obj;
            }, {});
        },
    },
    data() {
        return {
            duplicateModal: false,
            loading: true,
            listData: [],
            filteredList: [],
            duplicateData: {
                schema: {},
                name: null,
                id: null,
            },
            tableList: window.init["dbSchema"]["tableList"],
            viewList: window.init["dbSchema"]["viewList"],
        };
    },

    created() {
        this.getData();
    },

    methods: {

        getData() {
            axios.get(this.$props.src).then(({data}) => {
                this.loading = false;
                this.filteredList = this.listData = data.data;
            });
        },
        doDuplicate(){
            let src = "/lambda/puzzle/schema/form"
            if (this.type == "grid") {
                src = "/lambda/puzzle/schema/grid";

                this.duplicateData.schema.schema.forEach(col => {
                    col.table = this.duplicateData.schema.model
                });
            }
            // if(this.type == "from"){
            //   this.duplicateData.schema.ui.schema = [];
            // }

            let data = {
                name: this.duplicateData.name,
                schema: JSON.stringify(this.duplicateData.schema)
            };
            axios.post(src, data).then(({data}) => {
                if (data.status) {
                    this.$Notice.success({
                        title: `${this._form.savedSuccessfull}`,
                        desc: `"${this.formName}" ${this._form.formIformationSavedSuccessfull}`
                    });
                    this.duplicateModal = false;
                    this.duplicateData = {
                        schema: {},
                        name: null,
                        id: null,
                    };
                    this.getData();
                } else {
                    this.$Notice.error({
                        title: `${this._form.errorSaving}`,
                    });
                }
            });
        },
        duplicate(id, type) {

            let src = `/lambda/puzzle/schema/form/${id}/builder`
            if (this.type == "grid") {
                src = `/lambda/puzzle/schema/grid/${id}`
            }
            axios.get(src)
                .then(({data}) => {
                    this.duplicateData.name = data.data.hasOwnProperty('name') ? data.data.name : data.data.model;
                    this.duplicateData.schema = JSON.parse(data.data.schema);
                    this.duplicateData.id = id;
                    this.duplicateModal = true;
                }).catch(o => {
                console.log(o);
            });
        },
        deleteListItem(id) {
            axios.delete(`/lambda/puzzle/delete/vb_schemas/${this.type}/${id}`).then(o => {
                this.filteredList = this.filteredList.filter(
                    item => item.id !== id
                );
                this.listData = this.listData.filter(item => item.id !== id);
                this.$Message.success(`${this._form.successDeleted}`);
            });
        },
        cancel() {
        },
        filterList($event) {
            let vm = this;
            let q = $event.target.value;
            if (q != "") {
                vm.filteredList = vm.listData.filter(
                    item =>
                        item.name
                            .toString()
                            .toLowerCase()
                            .indexOf(q.toLowerCase()) >= 0
                );
            } else {
                vm.filteredList = vm.listData;
            }
        }
    }
};
</script>
